<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 x14>
      <v-card class="elevation-24">
        <v-toolbar dark color="blue darken-3">
          <v-toolbar-title>Acceso al sistema</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-text-field autofocus color="accent" label="Email" required v-model="email"></v-text-field>
          <v-text-field
            type="password"
            color="accent"
            label="Contrasea"
            required
            v-model="password"
          ></v-text-field>
        </v-card-text>
        <v-flex v-if="error" class="red--text">{{error}}</v-flex>
        <v-card-actions class="px-3 pb-3">
          <v-flex text-xs-right>
            <v-btn @click="ingresar" color="primary">Ingresar</v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      email: "",
      password: "",
      error: null
    };
  },
  methods: {
    ingresar() {
      this.error = null;
      axios
        .post("api/Usuarios/Login", {
          email: this.email,
          password: this.password
        })
        .then(resp => {
          return resp.data;
        })
        .then(data => {
          this.$store.dispatch("guardarToken", data.token);
          this.$router.push({ name: "home" });
        })
        .catch(error => {
          if (error.response.status == 400) {
            this.error = "No es un email vlido";
          } else if (error.response.status == 404) {
            this.error = "No existe el usuario o sus datos son incorrectos";
          } else {
            this.error = "Ocurrio un error";
          }
        });
    }
  }
};
</script>