<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Consulta Ventas</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        Desde:&nbsp;
        <v-text-field
        type="date"
          v-if="verNuevo==0"
          class="text-xs-center"
          v-model="fechaInicio"
        ></v-text-field>
        Hasta:&nbsp;
        <v-text-field
        type="date"
          v-if="verNuevo==0"
          class="text-xs-center"
          v-model="fechaFin"
        ></v-text-field>        
        <v-btn v-if="verNuevo == 0" @click="Listar" color="primary" dark class="mb-2">Buscar</v-btn>
        <v-dialog v-model="comprobanteModal" max-width="1000px">
          <v-card>
            <v-card-text>
              <v-btn @click="crearPDF()">
                <v-icon>print</v-icon>
              </v-btn>
              <div id="factura">
                <header>
                  <div id="logo">
                    <img src="data:image/png;base64," id="imagen" />
                  </div>
                  <div id="datos">
                    <p id="encabezado">
                      <b>IncanatoIT</b>
                      <br />Jos Glvez 1368, Chongoyape - Chiclayo, Per
                      <br />Telefono:(+51)931742904
                      <br />Email:jcarlos.ad7@gmail.com
                    </p>
                  </div>
                  <div id="fact">
                    <p>
                      {{tipo_comprobante}}
                      <br />{{serie_comprobante}}-{{num_comprobante}}
                      <br />{{fecha_hora}}
                    </p>
                  </div>
                </header>
                <br />
                <section>
                  <div>
                    <table id="facliente">
                      <tbody>
                        <tr>
                          <td id="cliente">
                            <strong>Sr(a). {{cliente}}</strong>
                            <br />
                            <strong>Documento:</strong> {{num_documento}}
                            <br />
                            <strong>Direccin:</strong> {{direccion}}
                            <br />
                            <strong>Telfono:</strong> {{telefono}}
                            <br />
                            <strong>Email:</strong> {{email}}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </section>
                <br />
                <section>
                  <div>
                    <table id="facarticulo">
                      <thead>
                        <tr id="fa">
                          <th>CANTIDAD</th>
                          <th>DESCRIPCIN</th>
                          <th>PRECIO UNIT</th>
                          <th>DESC.</th>
                          <th>PRECIO TOTAL</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="det in detalles" :key="det.iddetalle_venta">
                          <td style="text-align:center;">{{det.cantidad}}</td>
                          <td>{{det.articulo}}</td>
                          <td style="text-align:right;">{{det.precio.toFixed(2)}}</td>
                          <td style="text-align:right;">{{det.descuento}}</td>
                          <td style="text-align:right;">{{(det.cantidad * det.precio - det.descuento).toFixed(2)}}</td>
                        </tr>
                      </tbody>
                      <tfoot>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">SUBTOTAL</th>
                          <th style="text-align:right;">${{totalParcial =(total -totalImpuesto).toFixed(2)}}</th>
                        </tr>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">IGV ({{impuesto}}%)</th>
                          <th style="text-align:right;">${{totalImpuesto = ((total *impuesto) / (100+impuesto)).toFixed(2)}}</th>
                        </tr>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">TOTAL</th>
                          <th style="text-align:right;">{{total = (calcularTotal).toFixed(2)}}</th>
                        </tr>
                      </tfoot>
                    </table>
                  </div>
                </section>
                <br />
                <br />
                <footer>
                  <div id="gracias">
                    <p>
                      <b>Gracias por su compra!</b>
                    </p>
                  </div>
                </footer>
              </div>
              <v-btn @click="ocultarComprobante()" color="blue darken-1" flat>Cancelar</v-btn>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="ventas"
        class="elevation-1"
        v-if="verNuevo == 0"
      >
        <template v-slot:items="props">
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" color="blue darken-1" @click="verDetalles(props.item)">tab</v-icon>
            <v-icon
              small
              class="mr-2"
              color="blue darken-1"
              @click="mostrarComprobante(props.item)"
            >print</v-icon>
          </td>
          <td>{{ props.item.usuario }}</td>
          <td>{{ props.item.clientes }}</td>
          <td>{{ props.item.tipo_comprobante }}</td>
          <td>{{ props.item.serie_comprobante }}</td>
          <td>{{ props.item.num_comprobante }}</td>
          <td>{{ props.item.fecha_hora }}</td>
          <td>{{ props.item.impuesto }}</td>
          <td>{{ props.item.total }}</td>
          <td>
            <div v-if=" props.item.estado== 'Aceptado'">
              <span class="blue--text">Aceptado</span>
            </div>
            <div v-else>
              <span class="red--text">{{props.item.estado}}</span>
            </div>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="Listar">Resetear</v-btn>
        </template>
      </v-data-table>
      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo == 1">
        <v-layout row wrap>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="tipo_comprobante" :items="comprobantes" label="Tipo Comprobante"></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="serie_comprobante" label="Serie Comprobante"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="num_comprobante" label="Nmero Comprobante"></v-text-field>
          </v-flex>
          <v-flex xs12 sm8 md8 lg8 xl8>
            <v-select v-model="idcliente" :items="cliente" label="Cliente"></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="impuesto" label="Impuesto"></v-text-field>
          </v-flex>
          <v-flex xs8 sm8 md8 lg8 xl8>
            <v-text-field @keyup.enter="buscarCodigo()" v-model="codigo" label="Codigo"></v-text-field>
          </v-flex>
          <v-flex xs4 sm4 md4 lg4 xl4>
            <v-btn @click="mostrarArticulos()" small fab dark color="teal">
              <v-icon dark>list</v-icon>
            </v-btn>
          </v-flex>
          <v-flex xs4 sm4 md4 lg4 xl4 v-if="errorArticulo">
            <div class="red--text" v-text="errorArticulo"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <v-data-table
              :headers="cabeceraDetalles"
              :items="detalles"
              hide-actions
              class="elevation-1"
            >
              <template v-slot:items="props">
                <td>{{ props.item.articulo }}</td>
                <td>
                  <v-text-field type="number" v-model="props.item.cantidad"></v-text-field>
                </td>
                <td>
                  <v-text-field type="number" v-model="props.item.precio"></v-text-field>
                </td>
                <td>
                  <v-text-field type="number" v-model="props.item.descuento"></v-text-field>
                </td>
                <td>${{ props.item.cantidad * props.item.precio - props.item.descuento}}</td>
              </template>
              <template v-slot:no-data>
                <h3>No hay articulo agregados al detalle</h3>
              </template>
            </v-data-table>
            <v-flex class="text-xs-right">
              <strong>Total Parcial:</strong>
              $ {{totalParcial =(total- totalImpuesto).toFixed(2)}}
            </v-flex>
            <v-flex class="text-xs-right">
              <strong>Total Impuesto:</strong>
              $ {{totalImpuesto = ((total * impuesto)/(100+impuesto)).toFixed(2)}}
            </v-flex>
            <v-flex class="text-xs-right">
              <strong>Total Neto:</strong>
              $ {{total = (calcularTotal).toFixed(2)}}
            </v-flex>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg12 xl12>
            <v-btn @click="OcultarNuevo" color="blue darken-1" flat>Cancelar</v-btn>
            <v-btn v-if="verDet==0" @click="guardar()" color="success">Guardar</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from "axios";
import { log } from "util";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
  data() {
    return {
      valida: 0,
      validaMensaje: [],
      ventas: [],
      dialog: false,
      search: "",
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Usuario", value: "usuario" },
        { text: "Cliente", value: "cliente" },
        { text: "Tipo Comprobante", value: "tipo_comprobante" },
        {
          text: "Serie Comprobante",
          value: "serie_comprobante",
          sortable: false
        },
        {
          text: "Nmero Comprobante",
          value: "num_comprobante",
          sortable: false
        },
        { text: "Fecha", value: "fecha_hora", sortable: false },
        { text: "Impuestos", value: "impuestos", sortable: false },
        { text: "Total", value: "total", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      cabeceraDetalles: [
        //{ text: "Borrar", value: "borrar", sortable: false },
        { text: "Artculo", value: "articulo", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Subtotal", value: "subtotal", sortable: false }
      ],
      detalles: [],
      id: "",
      idcliente: "",
      cliente: [],
      tipo_comprobante: "",
      comprobantes: ["FACTURA", "BOLETA", "TICKET", "GUIA"],
      serie_comprobante: "",
      num_comprobante: "",
      impuesto: 16,
      codigo: "",
      verNuevo: 0,
      errorArticulo: null,
      totalParcial: 0,
      totalImpuesto: 0,
      total: 0,
      cabeceraArticulos: [
        { text: "Seleccionar", value: "seleccionar", sortable: false },
        { text: "Artculo", value: "articulo" },
        { text: "Categora", value: "categoria" },
        { text: "Descripcion", value: "descripcion", sortable: false },
        { text: "Stock", value: "stock", sortable: false },
        { text: "Precio Venta", value: "precio_venta", sortable: false }
      ],
      articulos: [],
      texto: "",
      verArticulo: 0,
      verDet: 0,
      adModal: 0,
      adAccion: 0,
      AdNombre: "",
      adId: "",
      comprobanteModal: 0,
      clientes: '',
      fecha_hora: '',
      num_documento: '',
      direccion: '',
      telefono: '',
      email: '',
      fechaInicio: '',
      fechaFin:''
    };
  },
  computed: {
    calcularTotal: function() {
      var resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          this.detalles[i].precio * this.detalles[i].cantidad -
          this.detalles[i].descuento;
      }
      return resultado;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.Listar();
    this.Select();
  },
  methods: {
    crearPDF(){
      var quotes = document.getElementById('factura');
      html2canvas(quotes).then(function (canvas){
        var imgData = canvas.toDataURL('image/png');
        var imgWidth = 210;
        var pageHeight= 295;
        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightleft = imgHeight;
        var doc = new jsPDF('p','mm', 'a4');
        var position =0;
         doc.addImage(imgData, 'PNG', 0 , position, imgWidth, imgHeight);
         doc.save("ComprobanteVenta.pdf");
      });
    },
    mostrarComprobante(item) {
      this.Limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante= item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.clientes = item.cliente;
      this.num_documento = item.num_documento;
      this.direccion = item.direccion;
      this.telefono = item.telefono;
      this.email = item.email;
      this.fecha_hora = item.fecha_hora;
      this.impuesto = item.impuesto;
      this.ListarDetalles(item.idventa)
      this.comprobanteModal = 1;
    },
    ocultarComprobante() {
      this.comprobanteModal = 0;
      this.Limpiar();
    },
    Select() {
      let me = this;
      var clientesArray = [];
      let headers = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: headers };
      axios
        .get("api/Personas/SelectClientes", configuracion)
        .then(resp => {
          clientesArray = resp.data;
          clientesArray.map(x => {
            me.cliente.push({ text: x.nombre, value: x.idpersona });
          });
        })
        .catch(error => {
          console.log(error);
        });
    },
    MostrarNuevo() {
      this.verNuevo = 1;
    },
    OcultarNuevo() {
      this.verNuevo = 0;
      this.Limpiar();
    },
    agregarDetalle(data = []) {
      this.errorArticulo = null;
      console.log(this.encuentra(data["idarticulo"]));

      if (this.encuentra(data["idarticulo"])) {
        this.errorArticulo = "El articulo ya ha sido agregado.";
      } else {
        this.detalles.push({
          idarticulo: data["idarticulo"],
          articulo: data["nombre"],
          cantidad: 1,
          precio: data["precio_venta"],
          descuento: 0
        });
      }
      this.verArticulo = 0;
      this.texto = "";
      this.articulos = [];
    },
    encuentra(id) {
      var sw = 0;
      for (var i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i].idarticulo == id) {
          sw = 1;
        }
      }
      return sw;
    },
    Listar() {
      let me = this;
      let headers = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: headers };
      let url = "";
      if (!me.fechaInicio || !me.fechaFin) {
        url = "api/Ventas/Listar";
      } else {
        url = "api/Ventas/ConsultaFechas/" + me.fechaInicio+'/'+me.fechaFin;
      }
      axios
        .get(url, configuracion)
        .then(resp => {
          me.ventas = resp.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    ListarDetalles(id) {
      let me = this;
      let headers = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: headers };
      axios
        .get("api/Ventas/ListarDetalles/" + id, configuracion)
        .then(resp => {
          me.detalles = resp.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    verDetalles(item) {
      this.Limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante = item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.idcliente = item.idcliente;
      this.impuesto = item.impuesto;
      this.ListarDetalles(item.idventa);
      this.verNuevo = 1;
      this.verDet = 1;
    },
    Limpiar() {
      this.id = "";
      this.idcliente = "";
      this.tipo_comprobante = "";
      this.serie_comprobante = "";
      this.num_comprobante = "";
      this.impuesto = 18;
      this.codigo = "";
      this.detalles = [];
      this.total = 0;
      this.totalImpuesto = 0;
      this.totalParcial = 0;
      this.verDet = 0;
    }
  }
};
</script>
<style>
#factura {
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

#logo {
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}
#imagen {
  width: 100px;
}

#fact {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}

#datos {
  float: left;
  margin-top: 0%;
  margin-left: 2%;
  margin-right: 2%;
  /*text-align: justify;*/
}

#encabezado {
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
}

section {
  clear: left;
}

#cliente {
  text-align: left;
}

#facliente {
  width: 40%;
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 15px;
}

#fa {
  color: #ffffff;
  font-size: 14px;
}

#facarticulo {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  padding: 20px;
  margin-bottom: 15px;
}

#facarticulo thead {
  padding: 20px;
  background: #2183e3;
  text-align: center;
  border-bottom: 1px solid #ffffff;
}

#gracias {
  text-align: center;
}
</style>